  methods: {
    updateTime(Sec) {
      let ly = Object.keys(this.lyrics).filter(key => {        
        return parseInt(key) < Sec;
      });                
      this.currentLyrics = this.lyrics[ly.pop()];
    }
  },
  created() {
    eventbus.$on("updateLyrics", this.updateTime);
  },
  beforeDestroy() {
    eventbus.$off("updateLyrics", this.updateTime);
  }
